import ApiClient from "../apiClient.js";

////////////////////////
/////// Call API
////////////////////////

const API_PREFIX = "/sdkapi";
const API_MODULE = "/action/call";
const API_VERSION = "/v1";

const API_NAME = API_PREFIX + API_MODULE + API_VERSION;

const apiClient = function () {
  if (typeof ApiClient.instance == "undefined" || ApiClient.instance == null) {
    const initErrorMsg = "Ipron server has not been initialized";
    console.error(initErrorMsg);
    throw new Error(initErrorMsg);
  }

  return ApiClient.instance;
};

/**
 *  
 * @param {String} tnt_id
 * @param {String} user_id
 * @param {String} call_id
 * @param {String} ani
 * @param {String} dnis
 * @param {String} user_ani
 * @param {String} media_type (voice|video|chat|email)
 * @returns axios
 */
export function makeCall(tnt_id, user_id, call_id, ani, dnis, user_ani, media_type) {
  return apiClient().axios.post(`${API_NAME}/makecall/${tnt_id}`, {
    tenant: tnt_id,
    user_id: user_id,
    call_id: call_id,
    ani: ani,
    dnis: dnis,
    user_ani: user_ani,
    media_type: media_type,
  });
}

/**
 *   ()
 * @param {String} tnt_id
 * @param {String} user_id
 * @param {String} ani
 * @param {String} dnis 
 * @param {String} user_ani  
 * @param {String} media_type  
 * @param {String} uei
 * @param {String} uui
 * @param {Object} route_option: {
      type: {int}                  // 0:, 1:, 2:, 3:
      priority: {int}              //  
      relation_method: {int}       // //   (0 : , 1 :  2 : )
      relation_agent_id: {string}  //  ID
      relation_timeout: {int}      //     
      method: {int}                //   (0 : Skill, 1 : Group, 2 : GroupSkill)
      skill_id: {string}           //    ID
      skill_level: {int}           //    
      group_id: {string}           //   GROUP ID
    }
 * @returns axios
 */
export function makeCallEx(tnt_id, user_id, ani, dnis, user_ani, media_type, uei, uui, route_option) {
  const params = {
    tenant: tnt_id,
    user_id: user_id,
    ani: ani,
    dnis: dnis,
    user_ani: user_ani,
    media_type: media_type,
    uei: uei,
    uui: uui,
  };

  params.route_opt = {
    type: route_option.type,
    priority: route_option.priority,
    relation_method: route_option.relation_method,
    relation_agent_id: route_option.relation_agent_id,
    relation_timeout: route_option.relation_timeout,
    method: route_option.method,
    skill_id: route_option.skill_id,
    skill_level: route_option.skill_level,
    group_id: route_option.group_id,
  };

  return apiClient().axios.post(`${API_NAME}/makecallex/${tnt_id}`, params);
}

