import ApiClient from "../apiClient.js";
import { decodeJWT } from "../../utils/util.js";
import * as constant from "../../common/constant.js";
import { addSubscriptions, delSubscriptions } from "../sse/notify.js";
import { userLogin, userLogout } from "../presence/presence.js";

////////////////////////
/////// Auth API ///////
////////////////////////

const API_PREFIX = "/sdkapi/sdk-auth";
const API_VERSION = "/v1/management";

const API_NAME = API_PREFIX + API_VERSION;

const apiClient = function () {
  if (typeof ApiClient.instance == "undefined" || ApiClient.instance == null) {
    const initErrorMsg = "Ipron server has not been initialized";
    console.error(initErrorMsg);
    throw new Error(initErrorMsg);
  }

  return ApiClient.instance;
};

/**
 *  
 * @param {Object} data
 */
function setLocalToken(data) {
  apiClient().authData = data;
  apiClient().userData = decodeJWT(data.accessToken);
  apiClient().axios.defaults.headers.common["Authorization"] = `Bearer ${data.accessToken}`;
}

/**
 *  
 */
function deleteLocalToken() {
  apiClient().authData = {};
  apiClient().userData = {};
  delete apiClient().axios.defaults.headers.common["Authorization"];
}

/**
 * 
 * @param {String} email
 * @param {String} plainPassword
 * @param {String} tntName
 * @param {String []} mediaSet
 * @param {String} state
 * @param {String} cause
 * @param {String} dn
 * @param {Function} eventCallback
 */
export async function login(email, plainPassword, tntName, mediaSet, state, cause, dn, eventCallback) {
  try {
    //     
    const fetchTokenResponse = await apiClient().axios.post(`${API_NAME}/token`, { email, plainPassword, tntName });
    if (!fetchTokenResponse?.result || !fetchTokenResponse?.data?.loginResult) return fetchTokenResponse;
    let accessToken = fetchTokenResponse?.data?.accessToken;
    //    
    setLocalToken(fetchTokenResponse?.data);
    // sse event subscribe   
    const { tntId, _id: userId } = apiClient().userData;
    const topic = `user/${userId}`;
    // sse event subscribe
    addSubscriptions(tntId, topic, eventCallback);
    return new Promise((resolve, reject) => {
      try {
        // sse event registered callback
        const eventRegisteredCallback = async () => {
          // CTI login 
          const userLoginResponse = await userLogin(tntId, userId, mediaSet, state, cause, dn);
          if (!userLoginResponse?.result) {
            //  eventSource close
            delSubscriptions(topic);
            deleteLocalToken();
            resolve(userLoginResponse);
            return;
          }
          //    accessToken  
          accessToken = userLoginResponse?.data?.accessToken;
          apiClient().axios.defaults.headers.common["Authorization"] = `Bearer ${accessToken}`;
          resolve(userLoginResponse);
        };
        // error  callback. .
        const eventErrorCallback = async () => {
          logout(tntId, userId, mediaSet, cause);
        };
        apiClient().eventMap[topic].addEventListener(constant.IPRON.EVENT.HANDLER.REGISTERED, eventRegisteredCallback);
        apiClient().eventMap[topic].addEventListener("error", eventErrorCallback);
      } catch (error) {
        console.error("%c[Login Error]", "background:red;color:#fff;font-weight:bold", error);
        reject(error);
      }
    });
  } catch (error) {
    console.error("%c[Login Error]", "background:red;color:#fff;font-weight:bold", error);
    return new Promise((resolve, reject) => {
      reject(error);
    });
  }
}

/**
 * 
 * @param {String} tntId
 * @param {String} userId
 * @param {String[]} media_set
 * @param {String} cause
 */
export async function logout(tntId, userId, media_set, cause) {
  return new Promise((resolve, reject) => {
    const doLogout = async () => {
      try {
        const topic = `user/${userId}`;
        delSubscriptions(topic);
        const userLogoutResponse = await userLogout(tntId, userId, media_set, cause);
        if (userLogoutResponse?.result) deleteLocalToken();
        resolve(userLogoutResponse);
      } catch (error) {
        reject(error);
      }
    };
    doLogout();
  });
}

/**
 *   .
 *         .
 * @param {String} email
 * @param {String} tntName
 */
export function setPasswordByEmail(email, tntName) {
  return apiClient().axios.post(`${API_NAME}/change-password/send-mail`, { email, tntName });
}
