import ApiClient from "../apiClient.js";
import * as constant from "../../common/constant.js";
import { createUUID } from "../../utils/util.js";

////////////////////////
/////// Notify API ///////
////////////////////////

const API_PREFIX = "/sdkapi/sse";
const API_MODULE = "/notify";
const API_VERSION = "/v1";

const API_NAME = API_PREFIX + API_VERSION + API_MODULE;
let __TNT_ID = "";

const apiClient = function () {
  if (typeof ApiClient.instance == "undefined" || ApiClient.instance == null) {
    const initErrorMsg = "Ipron server has not been initialized";
    console.error(initErrorMsg);
    throw new Error(initErrorMsg);
  }

  return ApiClient.instance;
};

/**
 *   
 * @param {String} topic
 * @returns EventSource
 */
export function getSubscriptions(topic) {
  return apiClient().eventMap[topic];
}

/**
 *  
 * @param {String} tntId
 * @param {String} topic
 * @param {function} eventCallback
 * @returns EventSource
 */
export function addSubscriptions(tntId, topic, eventCallback) {
  const defaultReq = apiClient().axios.defaults;
  const baseURL = defaultReq.baseURL;
  const headerToken = defaultReq.headers.common.Authorization;
  const headerTokenSplit = headerToken.split(" ");
  const clientId = apiClient().clientId;
  const params = {
    id: createUUID(),
    clientId,
    eventsubject: topic,
    bcloudToken: headerTokenSplit[1],
  };

  const paramsString = apiClient().axios.defaults.paramsSerializer(params);

  __TNT_ID = tntId;
  apiClient().eventMap[topic] = new EventSource(`${baseURL}${API_NAME}/${tntId}/subscribe/cti-client?${paramsString}`);

  const eventsource = apiClient().eventMap[topic];

  eventsource.addEventListener(constant.IPRON.EVENT.HANDLER.REGISTER, e => {
    const data = JSON.parse(e.data);
    if (apiClient().isDebug) {
      console.log(`${e.type} [${topic}] Event`, data);
    }
  });

  eventsource.addEventListener(constant.IPRON.EVENT.HANDLER.REGISTERED, e => {
    const data = JSON.parse(e.data);
    if (apiClient().isDebug) {
      console.log(`${e.type} [${topic}] Event`, data);
    }
  });

  eventsource.addEventListener(constant.IPRON.EVENT.HANDLER.PUSH, e => {
    const data = JSON.parse(e.data);
    if (apiClient().isDebug) {
      console.log(`${e.type} [${topic}] Event`, data);
    }
    eventCallback(data);
  });

  eventsource.addEventListener(constant.IPRON.EVENT.HANDLER.PROBEREQ, e => {
    const data = JSON.parse(e.data);
    if (apiClient().isDebug) {
      console.log(`${e.type} [${topic}] Event`, data);
    }
  });

  eventsource.addEventListener(constant.IPRON.EVENT.HANDLER.BANISHMENT, e => {
    const data = JSON.parse(e.data);
    if (apiClient().isDebug) {
      console.log(`${e.type} [${topic}] Event`, data);
    }
    const clientId = apiClient().clientId;
    const oldAppId = data?.data?.oldAppId;
    if (clientId === oldAppId) delSubscriptions(topic);
  });

  eventsource.onmessage = e => {
    const data = JSON.parse(e.data);
    console.log(` `, data);
  };

  /**
   *    ,   .
   *     .(23.09.26 LHJ)
   * */
  eventsource.onerror = e => {
    console.error(e);
    // delSubscriptions(topic);
    // setTimeout(() => {
    //   addSubscriptions(__TNT_ID, topic, eventCallback);
    // }, constant.TIMEOUT.RECONNECT);
  };

  apiClient().eventMap[topic] = eventsource;
}

/**
 *  , topic      
 * @param {String} subs_id
 */
export function delSubscriptions(topic) {
  if (topic) {
    if (apiClient().eventMap[topic] != null) {
      let eventsource = apiClient().eventMap[topic];
      eventsource.close();
      eventsource = null;
      delete apiClient().eventMap[topic];
      console.log(`Close EventSource [${topic}]`);
    }
  } else {
    this.eventMap = {};
  }
}

/**
 * user  
 * @param {String} tntId
 * @param {String} userId
 * @returns EventSource
 */
export function addUserSubscriptions(tntId, userId, eventCallback) {
  const topic = `user/${userId}`;
  this.addSubscriptions(tntId, topic, eventCallback);
}
export function delUserSubscriptions(userId) {
  const topic = `user/${userId}`;
  this.delSubscriptions(topic);
}

