import ApiClient from "../apiClient.js";

////////////////////////
/////// Info API ///////
////////////////////////

const API_PREFIX = "/sdkapi";
const API_MODULE = "/api";
const API_VERSION = "/v1";
const API_NAME = `${API_PREFIX}${API_MODULE}${API_VERSION}`;

const apiClient = function () {
  if (typeof ApiClient.instance == "undefined" || ApiClient.instance == null) {
    const initErrorMsg = "Ipron server has not been initialized";
    console.error(initErrorMsg);
    throw new Error(initErrorMsg);
  }

  return ApiClient.instance;
};

/**
 *    - 
 * @param {String} tenant_id
 * */
export function getGroupList(tenant_id) {
  return apiClient().axios.get(`${API_NAME}/groups/${tenant_id}`);
}

/**
 *  
 * @param {String} tenant_id
 * @param {String} group_id
 * */
export function getGroupInfo(tenant_id, group_id) {
  return apiClient().axios.get(`${API_NAME}/group/${tenant_id}/${group_id}`);
}

/**
 *    -  
 * @param {String} tenant_id
 * */
export function getAllAgentList(tenant_id) {
  return apiClient().axios.get(`${API_NAME}/users/${tenant_id}`);
}

/**
 *    - ,  
 * @param {String} tenant_id
 * @param {String} group_id
 * */
export function getAgentList(tenant_id, group_id) {
  return apiClient().axios.get(`${API_NAME}/users/${tenant_id}?groupId=${group_id}`);
}

/**
 *  
 * @param {String} tenant_id
 * @param {String} user_id
 * */
export function getAgentInfo(tenant_id, user_id) {
  return apiClient().axios.get(`${API_NAME}/user/${tenant_id}/${user_id}`);
}

/**
 *    - 
 * @param {String} tenant_id
 * */
export function getQueueList(tenant_id) {
  return apiClient().axios.get(`${API_NAME}/queues/${tenant_id}`);
}

/**
 *  
 * @param {String} tenant_id
 * @param {String} queue_id
 * */
export function getQueueInfo(tenant_id, queue_id) {
  return apiClient().axios.get(`${API_NAME}/queue/${tenant_id}/${queue_id}`);
}

